// src/App.js
import React, { useState } from 'react';
import { HashRouter as Router, Route, Routes, Link } from 'react-router-dom'; // Use HashRouter for GitHub Pages
import { Navbar, Nav, Container, Button } from 'react-bootstrap';
import { Elements } from '@stripe/react-stripe-js';
import { loadStripe } from '@stripe/stripe-js';

import Register from './components/Auth/Register';
import Login from './components/Auth/Login';
import BookList from './components/Books/BookList';
import BookDetail from './components/Books/BookDetail';
import ShoppingCart from './components/cart/ShoppingCart';
import UserProfile from './components/Profile/UserProfile';
import AdminDashboard from './components/Admin/AdminDashboard';
import AddBook from './components/Admin/AddBook';
import ManageBooks from './components/Admin/ManageBooks';
import Checkout from './components/Checkout/Checkout';
import Footer from './components/Footer/Footer';
import Home from './components/Home/Home';

// Replace with your actual publishable key
const stripePromise = loadStripe('pk_test_51QGRSQIoLxOczx23LXKYodQZDIC4USclSK0r0oJcrdwT2DVuTXgzoGoNKr9i64wI8CiIEfZrwFuMXEchN121BhJp00Mv86o3PB');

const App = () => {
    const [isAuthenticated, setIsAuthenticated] = useState(false);
    const [isAdmin, setIsAdmin] = useState(false);

    const handleLogin = (role) => {
        setIsAuthenticated(true);
        setIsAdmin(role === 'admin');
    };

    const handleLogout = () => {
        setIsAuthenticated(false);
        setIsAdmin(false);
        localStorage.removeItem('token');
        localStorage.removeItem('cart');
    };

    return (
        <Elements stripe={stripePromise}>
            <Router>
                <Navbar bg="light" expand="lg">
                    <Container>
                        <Navbar.Brand as={Link} to="/">My Bookstore</Navbar.Brand>
                        <Navbar.Toggle aria-controls="basic-navbar-nav" />
                        <Navbar.Collapse id="basic-navbar-nav">
                            <Nav className="me-auto">
                                {!isAuthenticated ? (
                                    <>
                                        <Nav.Link as={Link} to="/register">Register</Nav.Link>
                                        <Nav.Link as={Link} to="/login">Login</Nav.Link>
                                    </>
                                ) : (
                                    <>
                                        <Nav.Link as={Link} to="/profile">Profile</Nav.Link>
                                        <Nav.Link as={Link} to="/cart">Shopping Cart</Nav.Link>
                                        {isAdmin && <Nav.Link as={Link} to="/admin">Admin Dashboard</Nav.Link>}
                                        <Button variant="link" onClick={handleLogout}>Logout</Button>
                                    </>
                                )}
                                <Nav.Link as={Link} to="/books">Books</Nav.Link>
                            </Nav>
                        </Navbar.Collapse>
                    </Container>
                </Navbar>
                <Container className="mt-4">
                    <Routes>
                        <Route path="/" element={<Home />} />
                        <Route path="/register" element={<Register />} />
                        <Route path="/login" element={<Login onLogin={handleLogin} />} />
                        <Route path="/books" element={<BookList />} />
                        <Route path="/books/:id" element={<BookDetail />} />
                        <Route path="/cart" element={<ShoppingCart />} />
                        <Route path="/checkout" element={<Checkout />} />
                        <Route path="/profile" element={<UserProfile />} />
                        {isAdmin && (
                            <>
                                <Route path="/admin" element={<AdminDashboard />} />
                                <Route path="/admin/add-book" element={<AddBook />} />
                                <Route path="/admin/manage-books" element={<ManageBooks />} />
                            </>
                        )}
                        <Route path="*" element={<h2>404 Not Found</h2>} />
                    </Routes>
                </Container>
                <Footer />
            </Router>
        </Elements>
    );
};

export default App;

















